
export default function SendRequest(method, url, body = null) {
  return new Promise((resolve, reject) => { // resolve == , reject == 
    const xhr = new XMLHttpRequest(); //    API
    
    xhr.open(method, url); //   
    xhr.responseType = "json"; //     json
    xhr.setRequestHeader('Content-type', 'application/json'); //     json

    xhr.onload = () => {
      if (xhr.status >= 400) {
        reject(xhr.response); //   
      } else {
        resolve(xhr.response); //   
        resolve(xhr.status);
      }
    };
    xhr.onerror = () => {
      console.log(xhr.response); //    
    };

    xhr.send(JSON.stringify(body)); //       

  });
}
